.FileUpload {
  height: 180px;
  width: 180px;
  margin: 0 0 10px 0;
  border: solid var(--yellow) 1px;
  border-radius: 50%;
  text-align: center;
  background: var(--white);
}

.FileUpload h1 {
  margin: 2rem auto .6rem auto;
  width: 75%;
  font-family: var(--default-font);
  font-size: 1.5rem;
  font-weight: var(--title-weight);
  color: var(--purple);
}

.FileUpload__input {
  height: .01rem;
  width: .01rem;
}

.FileUpload__label {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-family: var(--default-font);
  padding: 0.20em 1.35em;
  font-size: 15px;
  color: var(--white);
  background-color: var(--pink);
}
